.navbar {
  border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  background-color: white;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 1;
}

.navbar-section {
  display: inline-block;
  margin: 16px 0;
}

.navbar-logo {
  padding: 0 16px;
  border-right: 1px solid rgba(0, 0, 0, 0.4);
  color: rgba(0, 0, 0, 0.4);
}

.navbar-logo-easy {
  color: black;
}

.navbar-link {
  color: rgba(0, 0, 0, 0.4);
  text-decoration: none;
  margin-left: 16px;
}

.navbar-link-active, .navbar-link:hover {
  color: black;
}

.footer {
  background-color: rgb(210, 210, 210);
  width: 100%;
  position: absolute;
  bottom: 0;
}

.footer > div {
  display: inline-block;
  padding: 16px;
  font-size: 12px;
}

.footer-right-section {
  float: right;
}

.main-container {
  min-height: 100vh;
  position: relative;
}

.search-input-container {
  width: 60vw;
  max-width: 750px;
  margin: 0 auto;
  padding-top: 20vh;
}

.search-input-field {
  font-size: 36px;
  width: calc(100% - 32px);
  padding: 16px;
  border-top: none;
  border-right: none;
  border-left: none;
}

.search-input-field:focus {
  outline: none;
}

.results-container, .favourites-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 2vw;
  width: 80vw;
  max-width: 1000px;
  margin: 0 auto;
}

.favourites-container {
  padding-top: 10vh;
  padding-bottom: 10vh;
}

.results-image {
  height: 200px;
  background-size: cover;
  border: 1px solid rgba(0, 0, 0, 0.4);
  display: inline-block;
  position: relative;
  z-index: 0;
  cursor: pointer;
}

.fetch-more-container {
  width: 80vw;
  max-width: 1000px;
  margin: 0 auto;
  padding-bottom: 10vh;
}

.fetch-more-button {
  margin: 36px 0;
  font-size: 18px;
  padding: 18px;
  display: block;
  width: 100%;
  border-radius: 5px;
  border-style: solid;
  cursor: pointer;
  background-color: rgb(210, 210, 210);
  -webkit-transition: background-color 0.2s;
  -o-transition: background-color 0.2s;
  transition: background-color 0.2s;
}

.fetch-more-button:hover {
  background-color: rgb(190, 190, 190);
}

.fetch-more-button:focus {
  outline: none;
}

.end-of-results, .no-results {
  text-align: center;
  font-size: 18px;
  width: 80vw;
  max-width: 1000px;
}

.is-favourite, .not-favourite {
  position: absolute;
  color: red;
  bottom: 1vh;
  right: 1vw;
  font-size: 36px;
}

.not-favourite {
  opacity: 0.5;
}

.spinner {
  padding-top: 10vh;
  text-align: center;
}

.snackbar {
  visibility: hidden;
  min-width: 250px;
  background-color: #333;
  color: #fff;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 1;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 60px;
}

.snackbar-visible {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

@-webkit-keyframes fadein {
    from {bottom: 0; opacity: 0;}
    to {bottom: 60px; opacity: 1;}
}

@keyframes fadein {
    from {bottom: 0; opacity: 0;}
    to {bottom: 60px; opacity: 1;}
}

@-webkit-keyframes fadeout {
    from {bottom: 60px; opacity: 1;}
    to {bottom: 0; opacity: 0;}
}

@keyframes fadeout {
    from {bottom: 60px; opacity: 1;}
    to {bottom: 0; opacity: 0;}
}

@media only screen and (max-width: 768px) {
  .search-input-field {
    font-size: 12px;
    padding: 8px;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .results-container, .favourites-container {
    display: block;
    width: 80vw;
    max-width: none;
    margin: 0 auto;
  }

  .results-image {
    height: 200px;
    width: 18vw;
    margin: 0 2vw 2vw 0;
    background-size: cover;
    border: 1px solid rgba(0, 0, 0, 0.4);
    display: inline-block;
    position: relative;
    z-index: 0;
    cursor: pointer;
  }

  .last-col {
    margin: 0 0 2vw 0;
  }
}

@media all and (max-width: 768px) and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .results-image {
    height: 200px;
    width: 100vw;
    margin-bottom: 2vw;
    background-size: cover;
    border: 1px solid rgba(0, 0, 0, 0.4);
    display: inline-block;
    position: relative;
    z-index: 0;
    cursor: pointer;
  }
}



